import React, { useRef } from "react";
import StatusBar from "../components/StatusBar";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faHashtag,
  faEnvelope,
  faFileArrowDown,
} from "@fortawesome/free-solid-svg-icons";

const FormField = ({ label, type, id, name, placeholder, required }) => (
  <li>
    <label htmlFor={id}>{label}</label>
    <span className="inputbox">
      {type === "textarea" ? (
        <textarea
          id={id}
          name={name}
          placeholder={placeholder}
          required={required}
        />
      ) : (
        <input
          type={type}
          id={id}
          name={name}
          placeholder={placeholder}
          required={required}
        />
      )}
    </span>
  </li>
);

const SocialMediaLink = ({ icon, title, href, onClick }) => (
  <li title={title} className={`bottom${icon}`}>
    <a href={href} onClick={onClick}>
      <FontAwesomeIcon icon={icon} />
    </a>
  </li>
);

const Contact = () => {
  const formFields = [
    {
      label: "( )*",
      type: "text",
      id: "name",
      name: "name",
      placeholder: "( ) .",
      required: true,
    },
    {
      label: "  ",
      type: "email",
      id: "email",
      name: "email",
      placeholder: "abc1234@sample.com",
      required: true,
    },
    {
      label: " ",
      type: "textarea",
      id: "message",
      name: "message",
      placeholder: " .",
    },
  ];

  const socialMediaLinks = [
    {
      icon: faHashtag,
      title: "  .",
      href: "https://www.instagram.com/xoy8n/",
    },
    {
      icon: faEnvelope,
      title: "  .",
      href: "_none",
      onClick: () => {
        copyToClipboard("s01057111917@gmail.com");
        alert(" .");
      },
    },
    {
      icon: faFileArrowDown,
      title: "   .",
      href: "./img/_.pdf",
      download: "_",
    },
  ];

  const copyToClipboard = (val) => {
    const t = document.createElement("textarea");
    document.body.appendChild(t);
    t.value = val;
    t.select();
    document.execCommand("copy");
    document.body.removeChild(t);
  };

  const closeBtnRef = useRef();

  React.useEffect(() => {
    if (closeBtnRef.current) {
      closeBtnRef.current.addEventListener("click", closeThankYouMessage);
    }

    return () => {
      if (closeBtnRef.current) {
        closeBtnRef.current.removeEventListener("click", closeThankYouMessage);
      }
    };
  }, []);

  const closeThankYouMessage = () => {
    const thkMsg = document.querySelector(".thankyou_message");
    thkMsg.style.display = "none";
  };

  return (
    <section id="contact">
      <div className="contact-wrap">
        <h5 className="main-name top">contact.</h5>
        <div className="contact left">
          <form
            className="gform form-elements"
            method="post"
            action="https://script.google.com/macros/s/AKfycbyyYpvkf9LvPxN3gpDwHp--i7lbFiTgbWubS8mvqDl1UmLgVzdfBJ9XeJ59m9dDN7kQUA/exec"
          >
            <fieldset>
              <StatusBar />
              <div className="formlist">
                <ul>
                  {formFields.map((field, index) => (
                    <FormField key={index} {...field} />
                  ))}
                </ul>

                <div className="btn_box">
                  <span className="comment">
                         .
                  </span>
                  <button type="submit" className="command_btn">
                    
                  </button>
                </div>
              </div>

              <div className="thankyou_message">
                <article>
                  <h1>   .</h1>
                  <p> .</p>
                  <div className="btn_box">
                    <span className="close"></span>
                  </div>
                </article>
              </div>
            </fieldset>
          </form>
        </div>
        <div className="sns_box bottom">
          <ul>
            {socialMediaLinks.map((link, index) => (
              <SocialMediaLink key={index} {...link} />
            ))}
          </ul>
        </div>
        <footer>
          <p> 2024 soyoon, All Rights Reserved.</p>
          <p>
                   
            .
          </p>
        </footer>
      </div>
    </section>
  );
};

export default Contact;
